import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  public vari=0;
  public varic=0;
  title = 'market';

  sh(){
    if(this.vari==0){
      this.vari=1;
    }
    else{
      this.vari=0;
    }
  }

  
  shc(){
    if(this.varic==0){
      this.varic=1;
    }
    else{
      this.varic=0;
    }
  }

}
